angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/hello.html","<div class=\"container-fluid\">\n    <div class=\"col-md-3\"></div>\n    <div class=\"col-md-6\">\n      <div class=\"row\">\n        <div class=\"page-header\">\n        <h1>Newsletter technique de Quantmetry<br><small>I\'m reading blog posts so you don\'t have to</small></h1>\n        <div class=\"input-group input-group-lg\">\n          <span class=\"input-group-addon\" id=\"sizing-addon1\">Recherche</span>\n          <input type=\"text\" class=\"form-control\" placeholder=\"search for words\" aria-describedby=\"sizing-addon1\" ng-model=\"test\" uib-typeahead=\"tag for tag in tags  | filter:$viewValue | limitTo:10\" typeahead-editable=\"true\">\n        </div>\n      </div>\n      </div>\n\n        <div class=\"row\">\n          <div ng-repeat=\"item in news | limitTo: limit as results\">\n            <news-item data=\"item\"></news-item>\n          </div>\n        </div>\n        <div class=\"row\">\n          <button type=\"button\" class=\"btn btn-primary btn-block\" ng-hide=\"results.length === news.length\" ng-click=\"limit = limit + limitExpand\">\n                Show {{limitExpand}} More ({{news.length - results.length}} remaining)\n          </button>\n        </div>\n    </div>\n    <div class=\"col-md-3\"></div>\n</div>\n\n<div class=\"footer\">\n  <p>Copyright (c) 2017</p>\n</div>\n");
$templateCache.put("app/newsitem/newsitem.html","<div class=\"panel panel-default\">\n  <div class=\"panel-heading\">\n    <h3 class=\"panel-title\">{{itemData.title}}</h3>\n    <p> {{itemData.date}} </p>\n  </div>\n  <div class=\"panel-body\">\n    {{itemData.text}}\n  </div>\n</div>\n");}]);